.career .content .title, .career .content .title-sec, .career .content .title-desc {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white; }

.career {
  overflow-x: hidden; }
  @media screen and (min-width: 820px) {
    .career {
      overflow: initial; } }
  .career .content .content-section {
    margin-top: 108px;
    margin-bottom: 108px; }
    @media screen and (min-width: 1440px) {
      .career .content .content-section {
        margin-top: 48px; } }
  .career .content .content-row {
    justify-content: center;
    margin-bottom: 24px; }
    .career .content .content-row .benefits-li .desc {
      font-size: 16px; }
      @media screen and (min-width: 786px) {
        .career .content .content-row .benefits-li .desc {
          max-width: 90%; } }
  .career .content .title {
    font-size: 24px;
    font-weight: 800;
    color: #354F53; }
    @media screen and (min-width: 1440px) {
      .career .content .title {
        font-size: 36px; } }
  .career .content .title-sec {
    font-size: 24px;
    font-weight: 800;
    color: #354F53; }
  .career .content .title-desc {
    font-size: 14px;
    color: #46737A; }
    @media screen and (min-width: 1440px) {
